import React from 'react';
import style from './AddMatch.module.css';
import { reduxForm, Field } from 'redux-form';
import { Redirect } from 'react-router';
import { required, maxLengthSymbols, minLengthSymbols } from './../../utils/validators/validators';
import { Input, Textarea } from './../../common/FormControls';
import { connect } from 'react-redux';
import { addMatch } from './../../redux/reducerMatches';

class AddMatch extends React.Component {

    state = {
        isCreatedMatch: false
    }

    render() {
        let onSubmitMatch = (values) => {
            let matchInfo = {
                firstTeam: values.firstTeam,
                secondTeam: values.secondTeam,
                score: values.score,
                comment: values.comment
            }
            console.log(matchInfo);
            this.setState({
                isCreatedMatch: true
            })
            this.props.addMatch(matchInfo);

        }

        if (this.state.isCreatedMatch) {
            return <Redirect to={"/matches"} />
        }

        return (
            <div className={style.addMatch}>
                <h2>Add Match to List</h2>
                <AddReduxForm onSubmit={onSubmitMatch} />
            </div>
        )
    }
}

const max = maxLengthSymbols(10);
const min = minLengthSymbols(2);

const addMatchForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={style.form}>
            <div className={style.box}>
                <label>First Team:
                        <Field name={"firstTeam"} validate={[required]} component={Input} placeholder="team" />
                </label>
            </div>
            <div className={style.box}>
                <label>Second Team:
                <Field name={"secondTeam"} validate={[required]} component={Input} placeholder="team" />
                </label>
            </div>
            <div className={style.box}>
                <label>Score:
                        <Field name="score" validate={[required]} placeholder="match of score" component={Input} />
                </label>
            </div>
            <div className={style.box}>
                <Field name="comment" validate={[max, min]} component={Textarea} placeholder="Your comment" />
            </div>
            <div className={style.btnLink}>
                <button>Send Match</button>
            </div>
        </form>
    )
}

const AddReduxForm = reduxForm({
    form: 'addmatch'
})(addMatchForm);

const mapStateToProps = (state) => ({
    matchCreated: state.matchesPage.matchCreated
})

export default connect(mapStateToProps, { addMatch })(AddMatch);