export const required = value => {
    if (value) return undefined;

    return "Field is required"
}

export const maxLengthSymbols = (maxLength) => {
    return values => {
        if (values && values.length > maxLength) return `Max symbols is ${maxLength}`;

        return undefined
    }
}

export const minLengthSymbols = (minLength) => {
    return values => {
        if(values && values.length > minLength) return undefined;

        return `Min symbols is ${minLength}`;
    }
}
